/* Styles for the error message */
.errmsg {
    color: red;
  }
  
  /* Styles for the input field and label */
  form {
    margin-top: 1rem;
  }
  
  label {
    margin-bottom: 0.5rem;
  }
  
  input {
    background: linear-gradient(0deg, #181A2D, #181A2D), linear-gradient(215.85deg, rgba(238, 238, 238, 0.46) -45.31%, rgba(238, 238, 238, 0) 168.95%);
    border: 1px solid var(--input-border);
    box-shadow: -3px 4px 4px 1px rgba(0, 0, 0, 0.32);
    appearance: none;
    border-radius: 0.75rem;
  }
  
  /* Styles for the instructional text */
  .instructions {
    margin-top: 0.5rem;
    font-size: 0.875rem; /* Adjust font size as needed */
    color: #666; /* Adjust color as needed */
  }

  .offscreen {
    display: none;
  }